.navbar {
  display: flex;
  justify-content: space-between;

  .logo {
    text-decoration: none;

    &__link {
      text-decoration: none;
      color: var(--black);
    }
  }

  .list-group {
    display: flex;
    align-items: center;

    .list-item {
      padding: 0 2rem;
      list-style: none;

      &:not(:last-child) {
        border-right: 0.25rem solid var(--black);
      }

      a {
        text-decoration: none;
      }
    }
  }
}
